import Typography from '@material-ui/core/Typography';
import React from 'react';
import { LinearProgress, Grid, Container } from '@material-ui/core';
import axios from 'axios';
import * as utilities from '../../utils/utilities';
import * as queries from '../../utils/queries';

export default class AreaExpansionPanelDetail extends React.Component < {area?: string, areaCode?: string} > {
    
    state = {
        ready: false,
        data: []
    }

    constructor(props) {

        super(props);
        this.state = {
            ready: false,
            data: []
        }
    }

    componentDidMount() {
        axios.get(utilities.createbaseURLWithQuery(queries.QUERY_TOTAL_CASES_BY_AREA))
            .then(response => {
                console.log(response.data);
            })
    }

    render () {
        return this.state.ready ? 
        <Container>
            <Grid><Typography variant="h5">Population: 122,124</Typography></Grid>
        </Container> : <LinearProgress />
    }
}